h1 {
  font-size: rem(136px);
}

h2 {
  font-size: rem(24px);
}

h3 {
  font-size: rem(22px);
}

h4 {
  font-size: rem(20px);
}

h5 {
  font-size: rem(18px);
}

h6 {
  font-size: rem(16px);
}

@font-face {
  font-family: AGaramondPro-Italic;
  src: url("../../fonts/AGaramondPro-Italic.ttf") format("truetype"), url("../../fonts/AGaramondPro-Italic.woff") format("woff"), url("../../fonts/AGaramondPro-Italic.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: AGaramondPro-Regular;
  src: url("../../fonts/AGaramondPro-Regular.ttf") format("truetype"), url("../../fonts/AGaramondPro-Regular.woff") format("woff"), url("../../fonts/AGaramondPro-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: Arial-Regular;
  src: url("../../fonts/Arial-Regular.ttf") format("truetype"), url("../../fonts/Arial-Regular.woff") format("woff"), url("../../fonts/Arial-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'icomoon';
  src: url("../../fonts/icomoon.eot?gdhs90");
  src: url("../../fonts/icomoon.eot?gdhs90#iefix") format("embedded-opentype"), url("../../fonts/icomoon.ttf?gdhs90") format("truetype"), url("../../fonts/icomoon.woff?gdhs90") format("woff"), url("../../fonts/icomoon.svg?gdhs90#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-plus1:before {
  content: "\e901";
}

.icon-arrow-right:before {
  content: "\e900";
}

html {
  box-sizing: border-box;
  font-size: 14px;
}

html,
body {
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "AGaramondPro-Regular", sans-serif;
  color: #a9a9a9;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

input,
button,
textarea,
select {
  border: none;
}

button {
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

textarea {
  resize: none;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
}

.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100vh;
  overflow-x: hidden;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container-fluid {
  max-width: 1800px;
  padding: 0 15px;
  margin: 0 auto;
}

.container {
  max-width: 1460px;
  padding: 0 15px;
  margin: 0 auto;
}

.coll-number {
  color: #a9a9a9;
  line-height: 30px;
  display: inline-block;
  width: 60px;
}

.row {
  display: flex;
}

.row:first-child {
  margin-bottom: 2.85714rem;
}

.row:last-child {
  align-items: flex-end;
}

.header {
  padding-top: 1.42857rem;
  text-align: center;
}

.header__logo svg {
  display: block;
  margin: 5px auto;
}

.footer {
  padding-bottom: 1.42857rem;
  text-align: center;
}

.see-link {
  font-size: 1.71429rem;
  color: #a9a9a9;
  line-height: 16px;
}

.see-link--close {
  text-decoration: line-through;
}

.see-link--open {
  text-decoration: none;
}

.main-collections {
  padding: 2.85714rem 0;
  position: relative;
}

.main-collections::before {
  content: 'Catalogue';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 27.64286rem;
  color: #ede0df;
  display: flex;
  align-items: center;
  justify-content: center;
}

.model-item {
  display: block;
  max-width: 285px;
  width: 100%;
  padding-left: 45px;
  position: relative;
}

.model-item::before {
  content: '\e900';
  position: absolute;
  font-family: 'icomoon';
  bottom: -12%;
  left: 0;
  color: #a9a9a9;
  font-size: 1.57143rem;
}

.model-item + .model-item {
  margin-left: 135px;
}

.model-item__image {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 300px;
  position: relative;
  height: 100%;
}

.model-item__image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
}

.model-item__number {
  position: absolute;
  top: 50px;
  left: 0;
  height: 0;
  width: 0;
  transform: rotate(-90deg);
  white-space: nowrap;
}

.model-item__title {
  position: absolute;
  bottom: 4%;
  left: 0;
  text-transform: capitalize;
  color: #000;
  letter-spacing: -0.64px;
}

.model-list--left {
  margin-left: 12.5rem;
}

.model-list--right {
  margin-right: auto;
}

.model-list li {
  margin-bottom: 1.07143rem;
}

.model-list li:last-child {
  margin-bottom: 0;
}

.model-list a {
  position: relative;
  width: 100%;
  padding-right: 7.14286rem;
}

.model-list a::before {
  font-family: 'icomoon';
  font-size: 1.57143rem;
  position: absolute;
  top: 0;
  right: 0;
  content: '\e900';
  color: #a9a9a9;
}

.model-list__number {
  letter-spacing: -.4px;
  margin-right: 5rem;
}

.model-list__title {
  color: #000;
  letter-spacing: -.4px;
}

@media (max-height: 790px) {
  .model-item {
    max-width: 240px;
  }
  .model-item__image {
    min-height: 240px;
  }
  .model-item__title {
    bottom: 6%;
  }
  .footer {
    margin-bottom: 0;
  }
}

@media (max-height: 730px) {
  .header {
    padding-top: 0.42857rem;
  }
  .main-collections {
    padding: 1.42857rem 0;
  }
  .model-item {
    max-width: 190px;
  }
  .model-item__image {
    min-height: 200px;
  }
  .model-item__title {
    font-size: 1.28571rem;
  }
  .model-list li {
    margin-bottom: 0.35714rem;
  }
}
